import { Link } from "gatsby"
import PropTypes from "prop-types"
import React, { Component } from "react"

// Compontents
import Navigation from "../Navigation/Navigation.component";
import HeaderImage from "../HeaderImage/HeaderImage.component";

// Images
import TonisityWhite from "../../images/logos/tonisity_logo_white.svg";

// Styles
import "./Header.styles.scss";

class Header extends Component{
  constructor(props) {
    super(props);
    this.state = {
      isHidden: true,
    };
  }

  toggleHidden() {
    this.setState({
      isHidden: !this.state.isHidden
    })
  }

  render(){

    return(
      <div>
        <header>
          <Link to="/">
            <img src={TonisityWhite} alt={this.props.siteTitle} />
          </Link>
          <button onClick={this.toggleHidden.bind(this)}>&#9776;</button>
        </header>
        <Navigation styleName={ `${this.state.isHidden ? `open + animate` : "close"}` } />
        <HeaderImage />
      </div>
    )
  }
}

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
